<template>
  <v-layout class="full-height" column align-center>
      <v-form v-model="isValid">
        <v-text-field
          label="Email"
          v-model="email"
          :rules="[notEmptyRule]"/>
        <v-text-field
          label="Password"
          type="password"
          v-model="password"
          :rules="[notEmptyRule]"/>
      </v-form>
        <v-btn 
          color="info"
          :disabled="!isValid"
          v-if="!isLoading"
          @click="signIn">
          Sign In
        </v-btn>
        <v-progress-circular
          v-else
          :size="70"
          :width="7"
          color="blue"
          indeterminate
        />
  </v-layout>
</template>

<script>
import store from "@/store";
import { mapState } from "vuex";
import { SIGN_IN } from "../vuex/actions.js";

export default {
  name: "signin",
  data: function() {
    return {
      isValid: false,
      email: "",
      password: "",
      notEmptyRule: v => v.length > 0 || "Can't be empty"
    };
  },
  computed: mapState(["isLoading"]),
  methods: {
    signIn() {
      store.dispatch(SIGN_IN, {
        email: this.email,
        password: this.password
      });
    }
  }
};
</script>

<style lang="sass" scoped>
  .full-height
    min-height: 100%
    justify-content: center
</style>
